import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, AlertController, App, ToastController } from 'ionic-angular';
import { AuthProvider } from '../../providers/auth/auth';

@IonicPage()
@Component({
  selector: 'page-other',
  templateUrl: 'other.html',
})
export class OtherPage {
  isAdmin: any;
  userId: any;
  constructor(
    public navCtrl: NavController, 
    public navParams: NavParams, 
    public alertCtrl: AlertController,
    public authProvider: AuthProvider,
    public app: App,
    public toastCtrl: ToastController
  ) {
    this.isAdmin = parseInt(localStorage.getItem('isAdmin'));
    this.userId = localStorage.getItem('userId');
  }

  goToPage(page) {
    this.navCtrl.push(page);
  }

  logout() {
    let confirm = this.alertCtrl.create({
      title: 'Logout Confirmation?',
      message: 'Are you sure do you want to log out?',
      buttons: [
        {
          text: 'Cancel',
          handler: () => {
          }
        },
        {
          text: 'Ok',
          handler: () => {
            this.authProvider.logout().then(() => {
              localStorage.clear();
              this.app.getRootNav().setRoot('HomePage');
            }).catch(err => {
              let toast = this.toastCtrl.create({
                message: err.message,
                duration: 5000,
                position: 'bottom'
              });

              toast.present();
            });
          }
        }
      ]
    });

    confirm.present();
  }

  switchRole() {
    let confirm = this.alertCtrl.create({
      title: 'Switch Role Confirmation?',
      message: 'Are you sure do you want to switch user role?',
      buttons: [
        {
          text: 'Cancel',
          handler: () => {
          }
        },
        {
          text: 'Ok',
          handler: () => {
            this.authProvider.switchRole(this.userId, 1).then(() => {
              this.navCtrl.push('AdminHomePage');
              let toast = this.toastCtrl.create({
                message: 'Switching role completed',
                duration: 5000,
                position: 'bottom'
              });

              toast.present();
            }).catch(err => {
              let toast = this.toastCtrl.create({
                message: err.message,
                duration: 5000,
                position: 'bottom'
              });

              toast.present();
            });
          }
        }
      ]
    });

    confirm.present();
  }
}



